@font-face {
  font-family: "OswaldRegular";
  src: url("../fonts/oswaldregular.woff"), url("../fonts/oswaldregular.woff2");
  font-weight: 400;
}
@font-face {
  font-family: "OswaldMedium";
  src: url("../fonts/oswaldmedium.woff"), url("../fonts/oswaldmedium.woff2");
  font-weight: medium;
}
.wrap {
  width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .wrap {
    width: 768px;
    padding: 0 30px;
    margin: 0 auto;
  }
}
@media (min-width: 1300px) {
  .wrap {
    width: 1300px;
    padding: 0 40px;
    margin: 0 auto;
  }
}
.reset {
  margin: 0;
  padding: 0;
}
.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}
.button {
  display: block;
  width: 280px;
  background-color: #68b738;
  color: #ffffff;
  border: none;
  font-family: "OswaldRegular", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
  padding: 10px 0;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: #99c684;
}
.button--gray {
  background-color: #f2f2f2;
  color: #111111;
}
.button--gray:hover {
  background-color: #ebebeb;
}
.button--gray:active {
  color: #c3c3c3;
}
.promo {
  position: relative;
}
.promo::after {
  content: "";
  position: absolute;
  width: 320px;
  height: 305px;
  top: 0;
  right: 0;
  margin: auto;
  left: 0;
  background-image: url("../img/index-background-mobile@1x.jpg");
  background-repeat: no-repeat;
  z-index: -1;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .promo::after {
    background-image: url("../img/index-background-mobile@2x.jpg");
    background-size: 320px auto;
  }
}
.promo::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 305px;
  background-color: #65a93c;
  background-repeat: no-repeat;
  z-index: -2;
}
.promo__inner {
  width: 320px;
  margin: 0 auto;
  padding: 26px 20px 22px 20px;
}
.promo__title {
  margin: 0;
  padding: 0;
  font-family: "OswaldRegular", "Arial", sans-serif;
  font-size: 36px;
  line-height: 36px;
  color: #ffffff;
  font-weight: 400;
  text-align: center;
  margin-bottom: 24px;
}
.promo__slogan {
  margin: 0;
  padding: 0;
  font-family: "OswaldRegular", "Arial", sans-serif;
  font-size: 14px;
  line-height: 14px;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 28px;
}
.promo__picture {
  margin: 0;
  padding: 0;
}
.promo__button {
  font-size: 14px;
  line-height: 18px;
}
@media (min-width: 768px) {
  .promo::after {
    display: none;
  }
  .promo::before {
    display: none;
  }
  .promo__inner {
    position: relative;
    width: 768px;
    padding: 31px 162px 0 118px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background-image: none;
  }
  .promo__title {
    width: 490px;
    font-size: 60px;
    line-height: 60px;
    color: #111111;
    text-align: left;
    margin-bottom: 40px;
  }
  .promo__slogan {
    font-size: 20px;
    line-height: 20px;
    color: #111111;
    text-align: start;
    margin-bottom: 50px;
  }
  .promo__picture {
    position: absolute;
    top: 232px;
    right: 30px;
    z-index: -1;
  }
  .promo__image {
    width: 709px;
    height: auto;
  }
  .promo__button {
    margin-bottom: 530px;
    width: 245px;
    font-size: 20px;
    padding: 17px 0;
  }
}
@media (min-width: 1300px) {
  .promo::after {
    content: "";
    position: absolute;
    top: -175px;
    left: 50%;
    right: 0;
    margin: 0;
    background-repeat: no-repeat;
    display: block;
    width: 720px;
    height: 696px;
    background-image: url("../img/index-background-desktop@1x.jpg");
    background-size: cover;
  }
  .promo::before {
    display: block;
    position: absolute;
    content: "";
    left: 50%;
    width: calc(50vw - 720px);
    background-color: #65a93c;
    height: 696px;
    top: -175px;
    left: auto;
    right: 0;
  }
  .promo__inner {
    width: 1300px;
    padding: 49px 690px 0 120px;
    background-position: top right;
    margin-bottom: 80px;
  }
  .promo__slogan {
    margin-bottom: 54px;
  }
  .promo__button {
    margin-bottom: 190px ;
    font-size: 20px;
    line-height: 20px;
    padding: 16px 0;
  }
  .promo__picture {
    top: -31px;
    right: 258px;
    z-index: 1;
  }
  .promo__image {
    width: 552px;
    height: auto;
  }
}
@media (min-width: 1300px) and (min-resolution: 144dpi), (min-width: 1300px) and (min-resolution: 1.5dppx) {
  .promo::after {
    background-image: url("../img/index-background-desktop@2x.jpg");
  }
}
